import React, { useState } from 'react';
import { Box, Button, Grid, TextField, Typography } from '@mui/material';

const VerifyCode: React.FC = () => {
  const [verificationCode, setVerificationCode] = useState('');
  const [error, setError] = useState('');
  const [isCodeResent, setIsCodeResent] = useState(false);

  const handleCodeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setVerificationCode(event.target.value);
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (!verificationCode) {
      setError('Por favor, insira o cdigo de verificao.');
      return;
    }
    setError('');
    // Lgica para processar a verificao do cdigo
    console.log('Cdigo de Verificao:', verificationCode);
  };

  const handleResendCode = () => {
    // Lgica para reenviar o cdigo de verificao
    setIsCodeResent(true);
    console.log('Cdigo de verificao reenviado.');
  };

  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '100vh',
        backgroundColor: '#f0f0f0',
      }}
    >
      <Box
        sx={{
          maxWidth: 400,
          padding: 4,
          borderRadius: 2,
          boxShadow: 3,
          backgroundColor: '#fff',
        }}
      >
        <Typography variant="h5" component="h1" gutterBottom>
          Confirmar Cdigo de Verificao
        </Typography>
        <form onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            {error && (
              <Grid item xs={12}>
                <Typography color="error">{error}</Typography>
              </Grid>
            )}
            <Grid item xs={12}>
              <TextField
                label="Cdigo de Verificao"
                fullWidth
                variant="outlined"
                value={verificationCode}
                onChange={handleCodeChange}
                required
              />
            </Grid>
            <Grid item xs={12}>
              <Button type="submit" variant="contained" color="primary" fullWidth>
                Confirmar Cdigo
              </Button>
            </Grid>
            <Grid item xs={12}>
              <Button
                variant="text"
                color="primary"
                fullWidth
                onClick={handleResendCode}
                disabled={isCodeResent}
              >
                {isCodeResent ? 'Cdigo Reenviado' : 'Reenviar Cdigo'}
              </Button>
            </Grid>
          </Grid>
        </form>
      </Box>
    </Box>
  );
};

export default VerifyCode;
